
import { inject } from "vue";
import Earth from "@/components/Earth/index.vue";
export default {
  name: "earth",
  components: { Earth },
  setup() {
    const setToolbarState: any = inject("setToolbarState");
    setToolbarState({
      routeControl: true,
      rotateControl: true,
      initControl: true,
      heatmapControl: true,
      flyLineControl: true,
      featurePointControl: true,
      videoFusion: false,
    });
  },
};
